<template>
  <div class="chat">
    <user-profile :userProfile="userProfile"></user-profile>
    <reviews-panel
      :likes="123" :reviews="messages.length"
      @showAll="changeMessages"
    >
    </reviews-panel>
    <user-reviews :messages="messagesShowed"></user-reviews>
    <input-form @sendMessage="sendMessage"></input-form>
  </div>
</template>

<script>
import UserProfile from '../UserProfile/UserProfile.vue'
import ReviewsPanel from '../ReviewsPanel/ReviewsPanel.vue'
import UserReviews from '../UserReviews/UserReviews.vue'
import InputForm from '../InputForm/InputForm.vue'

export default {
  name: 'chat',
  data: () => ({
    showAllMsg: false,
    defaultShowed: 3,
    currentShowed: 0,
    messages: [
      {
        username: '–°–∞–º—É–∏–ª',
        date: '13 –æ–∫—Ç—è–±—Ä—è 2011',
        text: '–°–∞–º—É–∏–ª  13 –æ–∫—Ç—è–±—Ä—è 2011 –ü—Ä–∏–≤–µ—Ç, –í–µ—Ä—É–Ω—å! –Ω–∏—á–µ —Å–µ–±–µ —Ç—ã –∫—Ä—É—Ç–∞—è. —Ñ–æ—Ç–∫–∞ –∫–ª–∞—Å—Å!!!!'
      },
      {
        username: '–õ–∏–ª–∏—è –°–µ–º—ë–Ω–æ–≤–Ω–∞',
        date: '14 –æ–∫—Ç—è–±—Ä—è 2011',
        text: '–í–µ—Ä–æ–Ω–∏–∫–∞, –∑–¥—Ä–∞–≤—Å—Ç–≤—É–π—Ç–µ! –ï—Å—Ç—å —Ç–∞–∫–æ–π –≤–æ–ø—Ä–æ—Å: –û—Å–æ–±—ã–π –≤–∏–¥ –∫—É–Ω–∏—Ü –∂–∏–∑–Ω–µ–Ω–Ω–æ —Å—Ç–∞–±–∏–ª–∏–∑–∏—Ä—É–µ—Ç –∫–∏–Ω–µ—Ç–∏—á–µ—Å–∫–∏–π –º–æ–º–µ–Ω—Ç, —ç—Ç–æ –∏ –µ—Å—Ç—å –≤—Å–µ–º–∏—Ä–Ω–æ –∏–∑–≤–µ—Å—Ç–Ω—ã–π —Ü–µ–Ω—Ç—Ä –æ–≥—Ä–∞–Ω–∫–∏ –∞–ª–º–∞–∑–æ–≤ –∏ —Ç–æ—Ä–≥–æ–≤–ª–∏ –±—Ä–∏–ª–ª–∏–∞–Ω—Ç–∞–º–∏?'
      },
      {
        username: '–õ–∏–ª–∏—è –°–µ–º—ë–Ω–æ–≤–Ω–∞',
        date: '14 –æ–∫—Ç—è–±—Ä—è 2011',
        text: '–í–µ—Ä–æ–Ω–∏–∫–∞, –∑–¥—Ä–∞–≤—Å—Ç–≤—É–π—Ç–µ! –ï—Å—Ç—å —Ç–∞–∫–æ–π –≤–æ–ø—Ä–æ—Å: –û—Å–æ–±—ã–π –≤–∏–¥ –∫—É–Ω–∏—Ü –∂–∏–∑–Ω–µ–Ω–Ω–æ —Å—Ç–∞–±–∏–ª–∏–∑–∏—Ä—É–µ—Ç –∫–∏–Ω–µ—Ç–∏—á–µ—Å–∫–∏–π –º–æ–º–µ–Ω—Ç?'
      }
    ],
    userProfile: {
      name: '–í–µ—Ä–æ–Ω–∏–∫–∞ –†–æ—Å—Ç–æ–≤–∞',
      position: '–ú–µ–Ω–µ–¥–∂–µ—Ä –ø–æ –ø—Ä–æ–¥–∞–∂–∞–º',
      description: '–ü–æ–¥–±–µ—Ä—É –¥–ª—è –≤–∞—Å —Å–∞–º—ã–µ –ª—É—á—à–∏–µ –ø—Ä–µ–¥–ª–æ–∂–µ–Ω–∏—è. –ú–æ–∏ —É—Å–ª—É–≥–∏ –∞–±—Å–æ–ª—é—Ç–Ω–æ –±–µ—Å–ø–ª–∞—Ç–Ω—ã.',
      graphs: [
        {
          category: '–†—É—á–Ω–æ–µ –±—Ä–æ–Ω–∏—Ä–æ–≤–∞–Ω–∏–µ',
          value: 11
        },
        {
          category: '–ü–∞–∫–µ—Ç–Ω—ã–µ —Ç—É—Ä—ã',
          value: 3
        },
        {
          category: '–û—Ç–µ–ª–∏',
          value: 1
        }
      ],
      totalValue: 15
    },
    newMessage: ''
  }),
  methods: {
    sendMessage (msg) {
      let date = new Date().toLocaleString('ru-Ru', {
        day: 'numeric',
        month: 'long',
        year: 'numeric'
      })
      this.messages.push({
        username: '–õ–∏–ª–∏—è –°–µ–º—ë–Ω–æ–≤–Ω–∞',
        date: date.split('–≥.')[0],
        text: msg
      })
      if (this.showAllMsg) {
        this.currentShowed = this.messages.length
      } else {
        this.currentShowed = this.defaultShowed
      }
    },
    changeMessages (showAll) {
      if (showAll) {
        this.currentShowed = this.messages.length
        this.showAllMsg = true
      } else {
        this.currentShowed = this.defaultShowed
        this.showAllMsg = false
      }
    }
  },
  computed: {
    messagesShowed () {
      return this.messages.slice(this.messages.length - this.currentShowed)
    }
  },
  created () {
    this.currentShowed = this.defaultShowed
  },
  components: {
    UserProfile,
    ReviewsPanel,
    UserReviews,
    InputForm
  }
}
</script>

<style lang="scss" src="./main.scss"></style>
